<template>
<div>
    <div class="row">
        <div class="col-12">
            <b-alert v-if="infoBar.text" show class="text-center" fade :variant="infoBar.variant">
                <h4>{{ infoBar.text }}</h4> 
                <b-spinner v-if="infoBar.spin" variant="success" label="Spinning"></b-spinner>
            </b-alert>
        </div>
    </div>
    <div class="row pt-3">
        <div class="col-sm-1 col-md-3"></div>
        <div class="col-sm-10 col-md-6">
            <form class="text-left">
                <div class="form-group">
                    <label for="base-email">E-mail адрес, на который будут отправляться заявки*</label>
                    <input type="email" v-model="registerForm.email" class="form-control" id="base-email" aria-describedby="emailHelp" placeholder="example@mail.com" required>
                    <small id="emailHelp" class="form-text text-muted">Вы всегда сможете его поменять в настройках</small>
                </div>
                <div class="form-group">
                    <label for="login">Логин, который вы будете использовать для входа*</label>
                    <input type="text" v-model="registerForm.login" class="form-control" id="login" placeholder="Введите логин" required>
                </div>
                <div class="form-group">
                    <label for="pass">Пароль, который вы будете использовать для входа*</label>
                    <input type="password" v-model="registerForm.password" class="form-control" id="pass" placeholder="Введите пароль" required>
                </div>
                <div class="form-group">
                    <label for="re-pass">Повторите пароль*</label>
                    <input type="password" v-model="registerForm.rePassword" class="form-control" id="re-pass" placeholder="Повторите пароль" required>
                </div>
                <div class="text-right">
                    <button type="submit" @click="formSend()" class="btn btn-primary">Сохранить</button>
                </div>
                
            </form>
        </div>
        <div class="col-sm-1 col-md-3"></div>
    </div>
</div>
</template>

<script>
export default {
    name: 'FirstAuth',
    props: ['baseUrl'],
    data: function() {
        return {
            registerForm: {
                email: "",
                login: "",
                password: "",
                rePassword: ""
            },
            infoBar: {
                text: "Здравствуйте! Пожалуйста, заполните все необходимые поля для дальнейшей работы",
                variant: "info",
                spin: false
            }
        }
    },
    methods: {
        formValidator: function() {
            if (!this.registerForm.email) {
                this.infoBar.text = "Пожалуйста, введите email!";
                this.infoBar.variant = "danger";
                return;
            }

            if (!this.registerForm.login) {
                this.infoBar.text = "Пожалуйста, введите логин!";
                this.infoBar.variant = "danger";
                return;
            }

            if (!this.registerForm.password) {
                this.infoBar.text = "Пожалуйста, введите пароль!";
                this.infoBar.variant = "danger";
                return;
            }

            if (!this.registerForm.rePassword) {
                this.infoBar.text = "Пожалуйста, повторите пароль!";
                this.infoBar.variant = "danger";
                return;
            }

            if (this.registerForm.password != this.registerForm.rePassword) {
                this.infoBar.text = "Пароли не совпадают! Проверьте, одинаковые ли пароли вы написали";
                this.infoBar.variant = "danger";
                return;
            }

            if (this.registerForm.email.length > 64) {
                this.infoBar.text = "Email слишком длинный!";
                this.infoBar.variant = "danger";
                return;
            }

            if (this.registerForm.login.length > 64) {
                this.infoBar.text = "Логин слишком длинный!";
                this.infoBar.variant = "danger";
                return;
            } 

            const reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
            
            if(reg.test(this.registerForm.email) == false) {
                this.infoBar.text = "Пожалуйста, введите корректный email!";
                this.infoBar.variant = "danger";
                return;
            }
            return true;
        },
        formSend: function() {
            event.preventDefault();
            if (this.formValidator() == true) {

                let addFormData = new FormData();

                for(let name in this.registerForm) {
                    addFormData.append(name, this.registerForm[name]);
                }

                fetch(this.baseUrl + 'app/api/firstAuth/', {
                    method: "POST",
                    body: addFormData
                })
                .then((response) => response.json())
                .then((data) => {
                    switch (data.status) {
                        case "success":
                            this.infoBar.text = "Добро пожаловать! :) Через несколько секунд вы сможете авторизоваться.";
                            this.infoBar.variant = "success";
                            this.infoBar.spin = true;
                            setTimeout(() => this.$router.push('/login'), 2000);
                            break;
                            
                        case "err_password":
                            this.infoBar.text = "Пароли не совпадают! Проверьте, одинаковые ли пароли вы написали";
                            this.infoBar.variant = "danger";
                            break;

                        case "err_valid":
                            this.infoBar.text = "Неправильно заполнены поля. Проверьте правильность заполненя";
                            this.infoBar.variant = "danger";
                            break;

                        case "err_exists":
                            this.infoBar.text = "Пользователь уже существует! Если у вас нет данных для входа, напишите на uni.co.ua@gmail.com";
                            this.infoBar.variant = "danger";
                            break;
                        default:
                            this.infoBar.text = "Ошибка! Попробуйте ещё раз или напишите на uni.co.ua@gmail.com";
                            this.infoBar.variant = "danger";
                            break;
                    }
                })
                .catch(() => {
                        this.infoBar.text = "Ошибка! Попробуйте ещё раз или напишите на uni.co.ua@gmail.com";
                        this.infoBar.variant = "danger";
                })
            }
        }
    },
    created: function() {
        console.log(this.baseUrl);
    }
}
</script>


