<template>
    <div>
        <div class="row">
            <div class="col-12 text-right pb-4">
                
            </div>
        </div>
        <div class="row">
            <div class="col-md-3 pb-3">
                <b-dropdown id="dropdown-1" text="Фильтровать заявки" variant="outline-primary" class="m-md-2">
                    <b-dropdown-item @click="monthFilter()">За этот месяц</b-dropdown-item>
                    <b-dropdown-item @click="yearFilter()">За этот год</b-dropdown-item>
                    <b-dropdown-item @click="allFilter()">За всё время</b-dropdown-item>
                </b-dropdown>
            </div>
            <div class="col-md-8 col-sm-12">
                <h3 v-if="allApps === true">Заявок пока нет :)</h3>
                <table class="table table-bordered text-left" v-else>
                    <thead>
                        <th scope="col">#</th>
                        <th scope="col">Инфо</th>
                    </thead>
                    <tbody v-if="filter == 'all'">
                        <tr :key="index" v-for="(app, index) in allApps" @click="getRequestById(app.id), viewedApp(index)" v-b-modal.modal-1>
                                <th v-if="!app.is_checked">{{ app.id }}</th>
                                <th v-if="!app.is_checked">{{ app.date }} | {{ app.client_ip }}: {{ app.country }}</th>
                                <td v-if="app.is_checked">{{ app.id }}</td>
                                <td v-if="app.is_checked">{{ app.date }} | {{ app.client_ip }}: {{ app.country }}</td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr :key="index" v-for="(app, index) in filteredApps" @click="getRequestById(app.id), viewedApp(index)" v-b-modal.modal-1>
                                <th v-if="!app.is_checked">{{ app.id }}</th>
                                <th v-if="!app.is_checked">{{ app.date }} | {{ app.client_ip }}: {{ app.country }}</th>
                                <td v-if="app.is_checked">{{ app.id }}</td>
                                <td v-if="app.is_checked">{{ app.date }} | {{ app.client_ip }}: {{ app.country }}</td>
                        </tr>
                    </tbody>
                </table>
                
            </div>
            <b-modal id="modal-1" title="BootstrapVue" ok-only>
                        <table class="table">
                            <thead>
                            </thead>
                            <tbody>
                                <tr :key="key" v-for="(app, key) in choosedApp">
                                    <th scope="row">{{ key }}</th>
                                    <td>{{ app }}</td>
                                </tr>
                            </tbody>
                        </table>
            </b-modal>
            <div class="col-md-1">
                
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Requests",
    props: ['baseUrl'],
    data: function() {
        return {
            allApps: [],
            filteredApps: [],
            choosedApp: {},
            filter: "all"
        }
    },
    methods: {
        getRequestById: function(appId) {
            let uData = localStorage.getItem("u_data");

            if (uData) {
                uData = JSON.parse(atob(uData));

                fetch(this.baseUrl + 'app/api/request/' + uData.login + '/' + uData.token + '/' + appId, {method: "GET"})
                .then(response => response.json())
                .then((data) => {
                    switch (data.status) {
                        case "success":
                            this.choosedApp = data.data;
                            break;

                        case "err_login":
                            localStorage.clear();
                            location.reload();
                            
                        default:
                            break;
                    }
                })
                .catch(() => {
                    console.log("Error with server connection");
                })
            }
        },
        viewedApp: function(index) {

            let uData = localStorage.getItem("u_data");

            if (uData) {
                uData = JSON.parse(atob(uData));
                
                let id;

                id = this.filteredApps[index].id;

                const dataSend = {
                    login: uData.login,
                    token: uData.token,
                    id: id
                };

                fetch(this.baseUrl + 'app/api/request/', {
                    method: "PUT",
                    body: JSON.stringify(dataSend)
                })
                .then(response => response.json())
                .then(data => {
                    switch (data.status) {
                        case "success":
                            this.filteredApps[index].is_checked = true;
                            break;
                        case "err_login":
                            localStorage.clear()
                            location.reload();
                            break;
                        default:
                            console.log("Error with application");
                    }
                })
                .catch(response => {
                    console.log("Error with server connection: " + response);
                })
            }
        },
        monthFilter: function() {
            this.filteredApps = this.allApps.filter((item, index) => {
                let date = new Date(item.date);
                let current = new Date;
                if (date.getMonth() == current.getMonth() && date.getFullYear() == current.getFullYear()) {
                    return item;
                }
            });
            this.filter = "filtered";
        },
        yearFilter: function() {
            this.filteredApps = this.allApps.filter((item, index) => {
                let date = new Date(item.date);
                let current = new Date;
                if (date.getFullYear() == current.getFullYear()) {
                    return item;
                }
            });
            this.filter = "filtered";
        },
        allFilter: function() {
            this.filteredApps = this.allApps;
            this.filter = "all";
        }
    },
    created: function() {
        let uData = localStorage.getItem("u_data");

        if (uData) {
            uData = JSON.parse(atob(uData));

            fetch(this.baseUrl + 'app/api/auth/' +  uData.login + '/' + uData.token, {method: "GET"})
            .then((response) => response.json())
            .then((data) => {
                switch (data.status) {
                    case "success":
                        break;
                    default:
                        localStorage.clear()
                        location.reload();
                        break;
                }
            })
            .catch(() => {
                localStorage.clear()
                location.reload();
            })
            
            fetch(this.baseUrl + 'app/api/request/' + uData.login + '/' + uData.token, {method: "GET"})
            .then((response) => response.json())
            .then((data) => {
                switch (data.status) {
                    case "success":
                        this.allApps = data.data;
                        this.monthFilter();
                        break;
                    case "err_login":
                        localStorage.clear()
                        location.reload();
                        break;
                    default:
                        console.log("something wrong");
                        break
                }
            })
            .catch(() => {
                console.log("catch worked");
            })
        }
    }
}
</script>

<style scoped>
tr {
    cursor: pointer;
}
</style>


