<template>
  <div>
    <div class="row pt-4">
      <div class="col-sm-1 col-md-3"></div>
      <div class="col-sm-10 col-md-6">
        <b-alert v-if="infoBar.text" show class="text-center" fade :variant="infoBar.variant"><h4>{{ infoBar.text }}</h4></b-alert>
      </div>
      <div class="col-sm-1 col-md-3"></div>
    </div>
    <div class="row">
      <div class="col-sm-1 col-md-4"></div>
      <div class="col-sm-10 col-md-4 text-left">
        <form>
          <div class="form-group">
            <label for="LoginUsername">Логин</label>
            <input type="text" class="form-control" v-model="loginForm.username" id="LoginUsername" placeholder="Введите логин" required>
          </div>
          <div class="form-group">
            <label for="loginPass">Пароль</label>
            <input type="password" class="form-control" v-model="loginForm.password" id="loginPass" placeholder="Введите пароль" required>
          </div>
          <div class="text-right">
            <button type="button" @click="login" class="btn btn-primary">Войти</button>
          </div>
        </form>
      </div>
      <div class="col-sm-1 col-md-4"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Auth',
  props: ["baseUrl"],
  data: function() {
      return {
        loginForm: {
          username: '',
          password: ''
        },
        infoBar: {
          text: "",
          variant: "info"
        }
      }
  },
  methods: {
    login: function() {
      fetch(this.baseUrl + "app/api/auth/", {
        method: "PUT",
        body: JSON.stringify(this.loginForm)
      })
      .then((response) => response.json())
      .then((data) => {
        switch (data.status) {
          case "success": 
            let uData = JSON.stringify({
              id: data.id,
              login: data.login,
              token: data.token
            })
            uData = btoa(uData);
            localStorage.setItem("u_data", uData);
            this.$emit("entered");
            
            break;
            default:
              this.infoBar.text = "Ошибка";
              this.infoBar.variant = "danger"

              // let a = localStorage.getItem("u_data");
              // console.log(JSON.parse(atob(a)));
        }
      })
    },
  }
 }
</script>


