// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import BootstrapVue from "bootstrap-vue"
import App from './App'
import router from './router'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.use(BootstrapVue)
Vue.config.productionTip = false

router.beforeEach((to, from, next) => {

  (async () => {

    let getUrl = window.location;
    let path = getUrl.pathname.split('/');
    path.splice(-2, 1);
    let baseUrl = getUrl.protocol + "//" + getUrl.host + path.join('/') + "application/";

    const response = await fetch(baseUrl + 'app/api/firstAuth/', {method: "GET"});
    const res = await response.json();
    let uData = localStorage.getItem("u_data");
    if(uData) {
      uData = JSON.parse(atob(uData));
    }
    if (res.status == "false" && to.path != "/") {
      if (uData && uData.id && uData.login && uData.token) {
        to.meta.login = true
        next()
      } else {
        if (to.path != "/login") {
          to.meta.login = false	
          next("/login")
        } else {
          next()
        }
      }
    } else if (res.status == "true" && to.path != "/") {
      next('/')
    } else if (res.status == "false" && to.path == "/") {
      next('/login')
    } else {
      next()
    }

    if (to.meta.login && to.path == '/login'){
      next('/requests')
    }

  })();

  
  // if(uData) {
  //   uData = JSON.parse(atob(uData));
  //   if (uData.id && uData.login && uData.token) {
  //     to.meta.login = true
  //     next()
  //   } else {
  //     if(to.path != '/login'){
  //       to.meta.login = false	
  //       next('/login')
  //     } else {
  //       next();
  //     }
  //   }
  //   console.log(uData);
  // } 
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



