<template>
  <div class="container">
    <div id="app">
      <Navbar class="pb-4 pt-0" v-if="login" :baseUrl="baseUrl" @logout="logout()"></Navbar>
      <router-view :baseUrl="baseUrl" @entered="logIn()"></router-view>
    </div>
  </div>
</template>

<script>

import Navbar from './components/Navbar'

export default {
  name: 'app',
  components: {
    Navbar
  },
  data: function() {
    return {
      login: ""
    }
  },
  methods: {
    logIn: function() {
      this.login = true;
      this.$router.push("/requests");
    },

    logout: function() {
      this.login = false;
      this.$router.push("/login");
    }
  },
  beforeCreate: function() {
    let getUrl = window.location;
    let path = getUrl.pathname.split('/');
    path.splice(-2, 1);
    this.baseUrl = getUrl.protocol + "//" + getUrl.host + path.join('/') + "application/";
  },
  created: function() {
    let uData = localStorage.getItem("u_data");
    if(uData) {
      uData = JSON.parse(atob(uData));
    } else {
      return;
    }

    if (uData.id && uData.login && uData.token) {
      this.login = true;
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}
</style>



