<template>
    <div>
        <div class="row">
            <div class="col-sm-1 col-md-1"></div>
            <div class="col-sm-10 col-md-10">
                <b-alert v-if="infoBar.text" show class="text-center" fade :variant="infoBar.variant"><h4>{{ infoBar.text }}</h4></b-alert>
            </div>
            <div class="col-sm-1 col-md-1"></div>
        </div>
        <div class="row">
            <div class="col-md-3 col-sm-1"></div>
            <div class="col-md-6 col-sm-10">
                <h3>Сменить e-mail</h3>
                <form class="text-left pt-3">
                    <div class="form-group">
                        <label for="changeEmail">Введите новый e-mail</label>
                        <input type="email" class="form-control" v-model="emailForm.newEmail" id="changeEmail" aria-describedby="emailHelp" placeholder="example@mail.com" required>
                    </div>
                    <div class="text-right">
                        <button type="submit" @click="editEmail()" class="btn btn-primary">Подтвердить</button>
                    </div>
                </form>
            </div>
            <div class="col-md-3 col-sm-1"></div>
        </div>
        <hr class="mt-3 mb-3">
        <div class="row">
            <div class="col-md-3 col-sm-1"></div>
            <div class="col-md-6 col-sm-10">
                <h3>Сменить пароль</h3>
                <form class="text-left pt-3">
                    <div class="form-group">
                        <label for="oldPass">Старый пароль</label>
                        <input type="password" v-model="passwordForm.oldPass" class="form-control" id="oldPass" placeholder="Введите пароль" required>
                    </div>
                    <div class="form-group">
                        <label for="newPass">Новый пароль</label>
                        <input type="password" v-model="passwordForm.newPass" class="form-control" id="newPass" placeholder="Введите пароль" required>
                    </div>
                    <div class="form-group">
                        <label for="reNewPass">Повторите пароль</label>
                        <input type="password" v-model="passwordForm.rePass" class="form-control" id="reNewPass" placeholder="Введите пароль" required>
                    </div>
                    <div class="text-right">
                        <button type="submit" @click="editPassword()" class="btn btn-primary">Сменить пароль</button>
                    </div>
                </form>
            </div>
            <div class="col-md-3 col-sm-1"></div>
        </div>
    </div>
</template>

<script>
export default {
    name: "EditProfile",
    props: ['baseUrl'],
    data: function() {
        return {
            infoBar: {
                variant: 'info',
                text: ''
            },
            emailForm: {
                newEmail: ""
            },
            passwordForm: {
                oldPass: "",
                newPass: "",
                rePass: ""
            }
        }
    },
    methods: {
        emailCheck: function() {
            const reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
            
            if(reg.test(this.emailForm.newEmail) == false) {
                this.infoBar.text = "Пожалуйста, введите корректный email!";
                this.infoBar.variant = "danger";
                return;
            }

            if (!this.emailForm.newEmail) {
                this.infoBar.text = "Пожалуйста, введите email!";
                this.infoBar.variant = "danger";
                return;
            }

            return true;
        },
        passwordCheck: function() {
            if(!this.passwordForm.oldPass) {
                this.infoBar.text = "Пожалуйста, введите текущий пароль!";
                this.infoBar.variant = "danger";
                return;
            }

            if(!this.passwordForm.newPass || !this.passwordForm.rePass) {
                this.infoBar.text = "Пожалуйста, введите новый пароль!";
                this.infoBar.variant = "danger";
                return;
            }

            if(this.passwordForm.newPass != this.passwordForm.rePass) {
                this.infoBar.text = "Пароли не совпадают!";
                this.infoBar.variant = "danger";
                return;
            }

            return true;
        },
        editEmail: function() {
            event.preventDefault();
            let checker = this.emailCheck();

            let uData = localStorage.getItem("u_data");
            if(uData && checker) {
                uData = JSON.parse(atob(uData));

                let dataSend = {
                    login: uData.login,
                    token: uData.token,
                    email: this.emailForm.newEmail
                }

                fetch(this.baseUrl + 'app/api/user/', {
                    method: "PUT", 
                    body: JSON.stringify(dataSend)
                })
                .then(response => response.json())
                .then(data => {
                    switch (data.status) {
                        case 'success':
                            this.infoBar.text = "E-mail успешно изменён!";
                            this.infoBar.variant = "success";
                            this.emailForm.newEmail = "";
                            break;
                        case 'err_login':
                            localStorage.clear();
                            location.reload();
                            break;
                        case 'err_email':
                            this.infoBar.text = "Пожалуйста, введите корректный email!";
                            this.infoBar.variant = "danger";
                            break;
                        case 'err_unknown':
                            this.infoBar.text = "Неизвестная операция! Что-то не так...";
                            this.infoBar.variant = "danger";
                            break;
                        default:
                            this.infoBar.text = "Возникла непредвиденная ошибка! Попробуйте ещё раз";
                            this.infoBar.variant = "danger";
                            break
                    }
                })
                .catch((err) => {
                    console.log("Error with server connection: " + err);
                })
            }
        },
        editPassword: function() {
            event.preventDefault();
            let checker = this.passwordCheck();

            let uData = localStorage.getItem("u_data");
            if(uData && checker) {
                uData = JSON.parse(atob(uData));

                let dataSend = {
                    login: uData.login,
                    token: uData.token,
                    oldPass: this.passwordForm.oldPass,
                    newPass: this.passwordForm.newPass,
                    rePass: this.passwordForm.rePass
                }

                fetch(this.baseUrl + 'app/api/user/', {
                    method: "PUT", 
                    body: JSON.stringify(dataSend)
                })
                .then(response => response.json())
                .then(data => {
                    switch (data.status) {
                        case 'success':
                            this.infoBar.text = "Пароль успешно изменён! Пожалуйста, перелогинтесь.";
                            this.infoBar.variant = "success";
                            this.passwordForm.oldPass = "";
                            this.passwordForm.newPass = "";
                            this.passwordForm.rePass = "";

                            setTimeout(() => {
                                localStorage.clear();
                                location.reload();
                                }, 2000);
                            break;
                        case 'err_login':
                            localStorage.clear();
                            location.reload();
                            break;
                        case 'err_rePassword':
                            this.infoBar.text = "Пароли не совпадают!";
                            this.infoBar.variant = "danger";
                            break;
                        case 'err_password':
                            this.infoBar.text = "Неправильный текущий пароль! Попробуйте снова.";
                            this.infoBar.variant = "danger";
                            break;
                        case 'err_unknown':
                            this.infoBar.text = "Неизвестная операция! Что-то не так...";
                            this.infoBar.variant = "danger";
                            break;
                        default:
                            this.infoBar.text = "Возникла непредвиденная ошибка! Попробуйте ещё раз";
                            this.infoBar.variant = "danger";
                            break
                    }
                })
                .catch((err) => {
                    console.log("Error with server connection: " + err);
                })

            }
        }
    }
}
</script>

<style scoped>
h4 {
    color: black!important;
}
</style>


