<template>
    <div>
        <b-navbar toggleable="lg" type="light" variant="light">

            <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

            <b-collapse id="nav-collapse" is-nav>
            <b-navbar-nav>
                <b-nav-item to="/requests">Заявки</b-nav-item>
            </b-navbar-nav>

            <!-- Right aligned nav items -->
            <b-navbar-nav class="ml-auto">

                <b-nav-item-dropdown right>
                <!-- Using 'button-content' slot -->
                <template slot="button-content">{{ login }}</template>
                <b-dropdown-item to="/settings">Настройки</b-dropdown-item>
                <b-dropdown-item href="#" @click="logout()">Выйти</b-dropdown-item>
                </b-nav-item-dropdown>
            </b-navbar-nav>
            </b-collapse>
        </b-navbar>
    </div>
</template>

<script>
export default {
    name: "Navbar",
    props: ['baseUrl'],
    data: function() {
        return {
            login: ""
        }
    },
    methods: {
        logout: function() {
            var data = localStorage.getItem("u_data")
            if (data) {
                data = JSON.parse(atob(data));
                var id = data.id;
            }
            fetch(this.baseUrl + 'app/api/auth/' + id, {method: "DELETE"})
            localStorage.clear("u_data");
            this.$emit("logout");
            // location.reload();
        }
    },
    created: function() {
        let data = localStorage.getItem("u_data")
        if (data) {
            data = JSON.parse(atob(data));
            this.login = data.login;
        }
    }
}
</script>

<style>
b-nav-item {
    color: white!important;
}
.white {
    color: white!important;
}
</style>


